(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('GameController', GameController);

	/** @ngInject */
	function GameController($log, $state, studentService, toastrService, HelperService, Session) {
		var vm = this;
		var student;
		var sizeScenes;
		var remainingQuestions = 0;
		vm.history;
		vm.index = 0;
		vm.active = "activated";

		init();

		/**
		* Eecupera todas questoes ainda no jogadas pelo aluno
		*/
		function init() {
			studentService.validateProfile();
			student = Session.refreshUserSession();
			getHistoryByStudentId(student.id);
		}

		function getHistoryByStudentId(studentId) {
			studentService.getHistory(studentId).then(function(response) {
				vm.history = response;
				sizeScenes = vm.history.length;
				verifyGameIsActived();
				calculateRemainingQuestions(vm.history);
			});
		}

		function calculateRemainingQuestions(scenes) {
			angular.forEach(scenes, function(scene) {
				if (!HelperService.isUndefinedOrNull(scene.question)) {
					remainingQuestions++;
				}
			})
		}

		/**
		* Envia um resposta do aluno ao back-end
		*/
		vm.sendAnswer = function(alternative) {
			var answerContext = {};
			answerContext.sceneIndex = vm.history[vm.index].index;
			answerContext.sceneId = vm.history[vm.index].id;
			answerContext.studentId = student.id;
			answerContext.skillId = vm.history[vm.index].question.skillId;
			answerContext.skillValue = alternative.skillValue;
			answerContext.remainingQuestions = --remainingQuestions;

			studentService.sendAnswer(answerContext).then(function(status) {
				if(status === 200) {
					vm.nextScene();
					return;
				}
				toastrService.showToastr(status);
			});
		}

		/**
		* Realiza a mudana de cena na tela
		*/
		vm.nextScene = function() {
			$log.log(vm.index);
			/** caso chege ao fim das cenas, deve levar aos resultados */
			if((vm.index + 1) == sizeScenes) {
				$log.log('fim do jogo, vai pra resultados');
				$state.go('student.result');
				return;
			}
			vm.index = vm.index + 1;
			vm.background = {'background-image' : "url(" + vm.history[vm.index].background.filename + ")"};
			if(vm.history[vm.index].question != null) {
				vm.active = "inactive";
				return;
			}
			vm.active = "active";
		}

		/**
		* Ativa o som do mouse hover
		*/
		vm.playhover = function () {
			var hover = document.getElementById("over");
			hover.currentTime = 0;
			hover.play();
		}

		vm.infoout = function () {
			$log.info('fora');
			var opic = document.getElementById("infobox");
			opic.className="infoclose";
		}

		var verifyGameIsActived = function () {
			if(student.isCompleted === true) {
				$state.go("^.result");
			} else if (sizeScenes === 0) {
				$state.go("^.noGame");
			} else {
				vm.background = {"background-image" : "url(" + vm.history[vm.index].background.filename +")"};
			}
		}
	}
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('MentorThemeController', MentorThemeController);

	/** @ngInject */
	function MentorThemeController($log, toastrService, mentorService, loginService) {
		var vm = this;
		var themeIdCurrent;
		vm.themeCurrent;

		var init = function() {
			mentorService.validateProfile();
			themeIdCurrent = loginService.getUserLogged().institution.gameThemeId;
			loadAllThemes();
		}

/** carrega todos temas disponiveis que esto habilitados pelo administrador 	*/
		function loadAllThemes() {
			mentorService.loadAllThemesActivated().then(function(response) {
				vm.allThemes = response;
				disableOthersThemes();
			});
		}

/** coloca todos temas vindo do server como false, somente colocando como true
o que est habilitado para o instituio */
		function disableOthersThemes() {
			$log.log("funo: disableOthersThemes, ID TEMA ATUAL => "+themeIdCurrent);
			var sizeArray = vm.allThemes.length;
			for (var i = 0; i < sizeArray; i++) {
				if (vm.allThemes[i].id == themeIdCurrent) vm.allThemes[i].active = true;
				else vm.allThemes[i].active = false;
			}
		}

		vm.updateThemeIdCurrent = function() {
			var institutionCode = loginService.getUserLogged().institution.code;
			mentorService.updateThemeIdCurrent(institutionCode, themeIdCurrent).then(function(status) {
				toastrService.showToastr(status);
			});
		}

/** manipula os switchs para que fique somente um ATIVO
ou todos DESATIVADOS  */
		vm.setThemeCurrent = function(theme) {
			if(themeIdCurrent == theme.id) {
				themeIdCurrent = 0;
			}
			else if(themeIdCurrent != 0) {
				vm.allThemes[themeIdCurrent - 1].active = false;
				themeIdCurrent = theme.id;
			}	else {
				themeIdCurrent = theme.id;
			}
		}

		init();

	}
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('MentorStudentController', MentorStudentController);

	/** @ngInject */
	function MentorStudentController($log, toastrService, mentorService, modalService, downloadService, Session, HelperService) {
		var vm = this;
		var page = {nextPage: 0, size: 20, isLast: false};
		vm.students = null;
		vm.isTechnical;
		vm.filter = {name: null, ra: null, course: {}};
		vm.tableHeader = [{label: "Nome", model: "name", width: {'width': '30%'}},
			{label: "RA", model: "ra", width: {'width': '15%'}},
			{label: "Curso", model: "course", width: {'width': '30%'}},
			{label: "Concludo", model: "completed", width: {'width': '10%'}},
			{label: "Ao", model: "action", width: {'width': '15%'}}];

    init();

    function init() {
			mentorService.validateProfile();
			vm.isTechnical = Session.refreshUserSession().institutionLevel === "TECHNICAL";
			loadAllCourses();
			loadStudents(true);

    }

		function loadStudents(clearCache) {
			if (clearCache) {
				page.nextPage = 0;
			}
			mentorService.loadStudents(vm.filter, clearCache, page).then(function(response) {
				page.nextPage++;
				page.isLast = response.remainingPages <= 0;
				vm.students = response.students;
			});
		}

		function loadAllCourses() {
			mentorService.loadAllCourses().then(function(response) {
				vm.allCourses = response;
			});
		}

    vm.openStudentModal = function(student) {
      modalService.setResult(student);
      var modalInstance = modalService.openModal('app/components/mentor/students/modal/student.modal.html', 'MentorStudentModalController');
			modalInstance.result.then(function () {
				loadStudents(true);
			});
		}

		vm.openDetailsModal = function(student) {
      modalService.setResult(student);
      modalService.openModal('app/components/mentor/students/modal/details.modal.html', 'MentorStudentModalController');
		}

		vm.openFileModal = function() {
			modalService.setResult(downloadTemplate);
      var modalInstance = modalService.openModal('app/shared/modal/file.modal.html', 'FileController');
			modalInstance.result.then(function (file) {
				sendFile(file);
			});
		}

		function downloadTemplate() {
			downloadService.template("student.xlsx");
		}

		function sendFile(file) {
      mentorService.sendFile(file).then(function(response) {
				toastrService.showToastr(response.status);
				loadStudents(true);
				modalService.okModal();
			});
    }

		vm.loadMoreStudents = function() {
			loadStudents(false);
		}

		vm.search = function() {
			page.nextPage = 0;
			page.isLast = false;
			vm.filter.course = HelperService.isUndefinedOrNull(vm.filter.course) ? {} : vm.filter.course;
			loadStudents(true);
		}
	}
})();

