(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.factory('storageService', storageService);

		/** @ngInject */
		function storageService() {
			return {
				setItem : _set,
				getItem : _get,
				removeItem : _remove,
				removeAll : _clearAll
			};

			function _set(key, value) {
				localStorage.setItem(key, angular.toJson(value));
			}

			function _get(key) {
				return angular.fromJson(localStorage.getItem(key));
			}

			function _remove(key) {
				localStorage.removeItem(key);
			}

			function _clearAll() {
				localStorage.clear();
			}

		}
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.factory('HelperService', HelperService);

		/** @ngInject */
		function HelperService() {
			return {
				isUndefinedOrNull: _isUndefinedOrNull,
				getSemester: _getSemester,
				getFullYear: _getFullYear
			};

			function _isUndefinedOrNull(arg) {
				return angular.isUndefined(arg) || arg == null || arg === "";
			}

			function _getSemester(date) {
				if (_isUndefinedOrNull(date)) {
					return _getSemester(new Date());
				}
				return date.getMonth() <= 5 ? 1 : 2;
			}

			function _getFullYear(date) {
				if (_isUndefinedOrNull(date)) {
					return _getFullYear(new Date());
				}
				return date.getFullYear();
			}
		}
})();

(function() {
	'use strict';

  angular
		.module('mapskillsWeb')
		.factory('downloadService',  downloadService);

		/** @ngInject */
		function downloadService($log, $q, $http, HelperService, API_SERVER) {
      return {
				template : _template,
				download : _download
			};

      function _template(fileName) {
        var a = document.createElement("a");
        document.body.appendChild(a);
        a.style = "display: none";
        $http({
            url: "assets/templates/".concat(fileName), method: "GET",
            headers: {'Content-type': 'application/json'},
            responseType: 'arraybuffer'
        }).success(function (data) {
            var blob = new Blob([data], {type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"});
            var objectUrl = URL.createObjectURL(blob);
            a.href = objectUrl;
            a.download = fileName;
            a.click();
            window.URL.revokeObjectURL(objectUrl);
        }).error(function (status) {
            $log.error(status);
        });
      }

			function _download(filter) {
				var a = document.createElement("a");
        document.body.appendChild(a);
        a.style = "display: none";
        $http({
            url: API_SERVER.REPORT.DOWNLOAD, method: "GET",
            headers: {'Content-type': 'application/json; charset=utf-8'},
						responseType: 'arraybuffer',
						params: {
							institutionLevel: filter.level,
							institutionCode: filter.institutionCode,
							courseCode: filter.courseCode,
							startYear: filter.startYear,
							startSemester: filter.startSemester,
							endYear: filter.endYear,
							endSemester: filter.endSemester,
							page: filter.page,
							size: filter.size
						}
        }).success(function (data) {
            var blob = new Blob([data], {type: "text/csv"});
            var objectUrl = URL.createObjectURL(blob);
            a.href = objectUrl;
            a.download = "report.csv";
            a.click();
            window.URL.revokeObjectURL(objectUrl);
        }).error(function (status) {
            $log.error(status);
        });
			}
		}
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.factory('toastrService', toastrService);

		/** @ngInject */
		function toastrService(toastr) {
			return {
        showToastr : _showToastr
			};

      function _showToastr(status) {
        switch (status) {
          case 200:
          case 201:
            toastr.success('Salvo com sucesso.', 'Feito!');
            break;
					case 400:
						toastr.error('Verifique as informaes de envio.', 'Falha!');
						break;
          case 401:
						toastr.error('Parece que voc no tem autorizao.', 'Ops!');
						break;
          case 403:
						toastr.error('Acesso Proibido.', 'Ops!');
						break;
          case 500:
            toastr.error('Parece que houve um problema no servidor.', 'Falha!');
            break;
          default:
            toastr.error('Parece que houve um problema no servidor.', 'Falha!');
            break;
        }
      }
		}
})();

(function() {
  'use strict';

  angular
    .module('mapskillsWeb')
    .directive('acmeSidebar', acmeSidebar);

  /** @ngInject */
  function acmeSidebar() {

    var directive = {
      restrict: 'E',
      templateUrl: function(element, attrs) {
        return "app/shared/sidebar/" + attrs.profile + ".html";
      },
      controller: SidebarController,
      controllerAs: 'sbCtrl',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function SidebarController(moment, $location, $state, storageService) {
      var vm = this;
      vm.isCollapsed = true;
      vm.changeClass = changeClass;
      vm.goPage = goPage;
      vm.ngClass = {dashboard : "", institutions : "",
                    themes : "", skills : "",
                    students : "", report : ""};

    init();
    /**
     * funo para ativao do class dos botes de navegao.
     */
     function changeClass(page) {
       var lastPage = storageService.getItem('page') || null;
       storageService.setItem('page', page);
       if(lastPage) {
         vm.ngClass[lastPage] = "null";
       }
       vm.ngClass[page] = "active";
       goPage(page);
     }

     function goPage(page) {
       $state.go("^." + page);
     }

     function init() {
       var url = $location.path();
       if(url.indexOf("/admin/theme/") !== -1) {
         $state.go("admin.themes");
         return;
       }
       var position = $location.path().lastIndexOf("/");
       vm.changeClass(url.substring(position + 1));
     }

    }
  }

})();

(function() {
  'use strict';

  angular
    .module('mapskillsWeb')
    .factory('Session', Session);

  /** @ngInject */
  function Session($cookies, application, $rootScope) {
    var createToken = function(tokenInfo) {
        var sessionObj = {
            token: tokenInfo
        };
        $cookies.put(application.authentication.mapskillsCookieKey, angular.toJson(sessionObj));
    };

    var createUser = function(userInfo) {
      var json = $cookies.get(application.authentication.mapskillsCookieKey);
      var sessionObj = angular.fromJson(json);
      sessionObj.user = userInfo;
      $cookies.put(application.authentication.mapskillsCookieKey, angular.toJson(sessionObj));
      this.user = userInfo;
      $rootScope.$broadcast('user:updated', this.user);
    }

    var destroy = function() {
        $cookies.remove(application.authentication.mapskillsCookieKey);
        this.user = null;
    };

    var hasSession = function() {
        return ($cookies.get(application.authentication.mapskillsCookieKey));
    };

    var refreshUserSession = function() {
        if (hasSession()) {
            var json = $cookies.get(application.authentication.mapskillsCookieKey);
            var obj = angular.fromJson(json);
            return obj.user;
        }
        return null;
    };

    var token = function() {
        if (hasSession()) {
            var json = $cookies.get(application.authentication.mapskillsCookieKey);
            var obj = angular.fromJson(json);
            return obj.token;
        }
        return null;
    };

    var userPrivileges = function() {
        if (hasSession()) {
            var json = $cookies.get(application.authentication.mapskillsCookieKey);
            var obj = angular.fromJson(json);
            return obj.user.privileges;
        }
    };
    var service = {
      createToken: createToken,
      createUser: createUser,
      destroy: destroy,
      hasSession: hasSession,
      refreshUserSession: refreshUserSession,
      token: token,
      userPrivileges: userPrivileges
    };
    return service;
  }
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.factory('reportService', reportService);

		/** @ngInject */
		function reportService($http, $q, $log, HelperService, downloadService, API_SERVER) {
			return {
				search : _search,
        download : _download
			};

      function _search(filter) {
        var deferred = $q.defer();
        var uri = API_SERVER.REPORT.SHOW
        $http.get(uri, {
					params: {
							institutionLevel: filter.level,
							institutionCode: filter.institutionCode,
							courseCode: filter.courseCode,
							startYear: filter.startYear,
							startSemester: filter.startSemester,
							endYear: filter.endYear,
							endSemester: filter.endSemester,
							page: filter.page,
							size: filter.size
						}
					}).then(function success(response) {
						deferred.resolve(response.data);
        }, function error(response) {
          $log.error(response);
        });
        return deferred.promise;
      }

      function _download(filter) {
        downloadService.download(filter);
      }

		}
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('ReportController', ReportController);

	/** @ngInject */
	function ReportController($log, adminService, Session, HelperService, reportService) {
		var vm = this;
    vm.isAdmin = true;
    vm.filter = {level: null, institutionCode: null, courseCode: null, startDate: null, endDate: null};
    vm.institutions = [];
    vm.institutionSelected;
    vm.courseSelected;

    vm.report = {};

		function init() {
			var userLogged = Session.refreshUserSession();
			vm.isAdmin = verifyIsAdmin(userLogged);
			if(vm.isAdmin) {
				adminService.loadAllInstitutions(true).then(function(response) {
					angular.copy(response, vm.institutions);
				});
			} else {
				vm.getInstitutionCourses(userLogged.institution.id);
			}
		}
		/*
		* Chama do servio de download com condicionais do filtro.
		*/
    vm.download = function() {
      fillFilter();
			reportService.download(vm.filter);
    }
		/*
		* Realiza chamada para busca do relatrio.
		*/
    vm.search = function() {
      fillFilter();
			$log.log(vm.filter);
			reportService.search(vm.filter).then(function(response) {
				vm.report = [];
				vm.report = angular.copy(response);
			});
    }

		vm.getInstitutionCourses = function(id) {
			if(angular.isUndefined(id)) {
				return;
			}
			adminService.getInstitutionDetails(id).then(function(response) {
				vm.institutionSelected = angular.copy(response);
			});
		}
    // preenche o filtro para realizao da pesquisa ou download do csv
    var fillFilter = function() {
      vm.filter["level"] = HelperService.isUndefinedOrNull(vm.institutionSelected) ? null : angular.copy(vm.institutionSelected.level);
      vm.filter["institutionCode"] = HelperService.isUndefinedOrNull(vm.institutionSelected) ? null : angular.copy(vm.institutionSelected.code);
      vm.filter["courseCode"] = HelperService.isUndefinedOrNull(vm.courseSelected) ? null : angular.copy(vm.courseSelected.code);
			vm.filter["startYear"] = vm.filter.startDate === "" ? null : getYear(vm.filter.startDate);
			vm.filter["endYear"] = vm.filter.startDate === "" ? null : getYear(vm.filter.endDate);
			vm.filter["startSemester"] = vm.filter.endDate === "" ? null : getSemester(vm.filter.startDate);
			vm.filter["endSemester"] = vm.filter.endDate === "" ? null : getSemester(vm.filter.endDate);
			vm.filter["page"] = 0;
			vm.filter["size"] = 50;
    }

		var verifyIsAdmin = function(user) {
			return user.profile === "ADMINISTRATOR";
		}

		var getYear = function(filterDate) {
			return HelperService.isUndefinedOrNull(filterDate) ? "" : filterDate.split("/")[0];
		}

		var getSemester = function(filterDate) {
			return HelperService.isUndefinedOrNull(filterDate) ? "" : filterDate.split("/")[1];
		}

		init();

	}

})();

